// #region [Imports] ===================================================================================================
// Contexts
import CustomersContextProvider from "../../contexts/customersQuery";
// Components
import Dashboard from "../Dashboard";
import Customers from "../Customers";
import Settings from "../Settings";
import License from "../License";
// #endregion [Interfaces]
// #region [Component] =================================================================================================
var TabSwitch = function (props) {
    var slug = props.slug;
    if ("dashboard" === slug)
        return acfwpElements.element.createElement(Dashboard, null);
    if ("customers" === slug)
        return (acfwpElements.element.createElement(CustomersContextProvider, null,
            acfwpElements.element.createElement(Customers, null)));
    if ("settings" === slug)
        return acfwpElements.element.createElement(Settings, null);
    if ("license" === slug) {
        var is_multisite = acfwAdminApp.loyalty_program.license.is_multisite;
        if (is_multisite) {
            window.location.href = acfwAdminApp.loyalty_program.license.license_page;
            return null;
        }
        return acfwpElements.element.createElement(License, null);
    }
    return null;
};
export default TabSwitch;
// #endregion [Component]
