// #region [Imports] ===================================================================================================
// Actions
import { ELicenseActionTypes, } from "../actions/license";
// #endregion [Imports]
// #region [Reducer] ===================================================================================================
export default (function (license, action) {
    if (license === void 0) { license = null; }
    switch (action.type) {
        case ELicenseActionTypes.SET_LICENSE: {
            var data = action.payload.data;
            return data;
        }
        default:
            return license;
    }
});
// #endregion [Reducer]
