// #region [Imports] ===================================================================================================
import axios from "axios";
var CancelToken = axios.CancelToken;
// #endregion [Variables]
// export axios instance
export default axios.create({
    baseURL: wpApiSettings.root,
    timeout: 30000,
    headers: { "X-WP-Nonce": wpApiSettings.nonce },
});
// variable to save all axios cancels.
var axiosCancelMap = new Map();
// export axios cancel method.
export var axiosCancel = function (id) {
    var cancel = axiosCancelMap.get(id);
    if (cancel) {
        cancel();
        axiosCancelMap.delete(id);
    }
};
// export cancel token.
export var getCancelToken = function (id) {
    return new CancelToken(function (c) { return axiosCancelMap.set(id, c); });
};
