// #region [Imports] ===================================================================================================
// #endregion [Action Payloads]
// #region [Action Types] ==============================================================================================
export var ELicenseActionTypes;
(function (ELicenseActionTypes) {
    ELicenseActionTypes["READ_LICENSE"] = "READ_LICENSE";
    ELicenseActionTypes["ACTIVATE_LICENSE"] = "ACTIVATE_LICENSE";
    ELicenseActionTypes["SET_LICENSE"] = "SET_LICENSE";
})(ELicenseActionTypes || (ELicenseActionTypes = {}));
// #endregion [Action Types]
// #region [Action Creators] ===========================================================================================
export var LicenseActions = {
    readLicenseData: function (payload) { return ({
        type: ELicenseActionTypes.READ_LICENSE,
        payload: payload,
    }); },
    activateLicenseData: function (payload) { return ({
        type: ELicenseActionTypes.ACTIVATE_LICENSE,
        payload: payload,
    }); },
    setLicenseData: function (payload) { return ({
        type: ELicenseActionTypes.SET_LICENSE,
        payload: payload,
    }); },
};
