// #region [Imports] ===================================================================================================
// CSS
import "./index.scss";
// Components
import CustomersList from "./CustomersList";
import SingleCustomer from "./SingleCustomer";
// Actions
import { CustomerDataActions } from "../../store/actions/customers";
var useLocation = acfwpElements.router.useLocation;
var readCustomerStatus = CustomerDataActions.readCustomerStatus, readCustomerHistory = CustomerDataActions.readCustomerHistory;
// #endregion [Variables]
// #region [Interfaces] ================================================================================================
// #endregion [Interfaces]
// #region [Component] =================================================================================================
var Customers = function () {
    var urlParams = new URLSearchParams(useLocation().search);
    var customerID = urlParams.get("customer");
    if (customerID) {
        return acfwpElements.element.createElement(SingleCustomer, { customerID: parseInt(customerID) });
    }
    return (acfwpElements.element.createElement(CustomersList, null));
};
export default Customers;
// #endregion [Component]
